import React from 'react';
import background from '../assets/Background_v2.png';
import nino from '../assets/Nino_v2.png';
import rbtn from '../assets/Recap_btn.png';
import mbtn from '../assets/Measure_btn.png';
import logo from '../assets/Logo.png';
import './GameLayout.css';
import { useNavigate } from 'react-router-dom'; // js  
import { useState } from 'react'; // 

// 
const GameButton = ({ src, alt, onClick, top, left, width, preventActions }) => {
  const [pressed, setPressed] = useState(false);

  return (
    <img 
      src={src} 
      alt={alt}
      onClick={onClick}
      //pc 
      onMouseDown={() => setPressed(true)}
      onMouseUp={() => setPressed(false)}
      onMouseLeave={() => setPressed(false)}
      // 
      onTouchStart={() => setPressed(true)}
      onTouchEnd={() => setPressed(false)}
      style={{
        position: 'absolute',
        top: top,
        left: left,
        transform: `translate(-50%, -50%) scale(${pressed ? 0.92 : 1})`,
        width: width,
        zIndex: 20,
        cursor: 'pointer',
        transition: 'transform 0.1s ease',
        WebkitTapHighlightColor: 'transparent'
      }}
      onContextMenu={preventActions}
      onDragStart={preventActions}
    />
  );
};

function GameLayout() {
  const preventActions = (e) => e.preventDefault(); // 

  const navigate = useNavigate(); //  

  const recapClick = () => {
    navigate('/recap-main'); // App.js  path 
  };
  const testClick = () => {
    navigate('/name'); // App.js  path 
  };

  return (
    <div style={{ 
      width: '100vw', 
      height: '100vh', 
      backgroundColor: '#ffffff', // 
      display: 'flex', 
      alignItems: 'center', 
      justifyContent: 'center', 
      overflow: 'hidden' 
    }}>

    <div style={{ 
        position: 'relative', 
      //     (16:9)  ' '  
      width: 'min(100vw, calc(100vh * 1920 / 1080))',
      height: 'min(100vh, calc(100vw * 1080 / 1920))',
      backgroundColor: 'black', //   (   )
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      //boxShadow: '0 0 20px rgba(0,0,0,0.2)' //   
    }}>

        <img 
          src={background} 
          alt=""
          style={{
            width: '100%',
            height: '100%',
            //objectFit: 'fill', //   
            display: 'block'
          }}
          onContextMenu={preventActions}
          onDragStart={preventActions}
        />

        <img 
          src={nino} 
          alt=""
          style={{
            position: 'absolute',
            top: '50%',          //  50% 
            left: '49.95%',         //  "
            transform: 'translate(-50%, -50%)', //    
            width: '100%',      //   
            zIndex: 10
          }}
          onContextMenu={preventActions}
          onDragStart={preventActions}
        />

        <img 
          src={logo} 
          alt=""
          className="tilting-logo"
          style={{
            position: 'absolute',
            top: '49%',          
            left: '49.95%',         
            transform: 'translate(-50%, -50%)', //    
            width: '100%',      //   
            zIndex: 15
          }}
          onContextMenu={preventActions}
          onDragStart={preventActions}
        />

        <GameButton 
          src={mbtn} 
          alt=" " 
          onClick={testClick} 
          top="66.7%" 
          left="24.1%" 
          width="11.5%" 
          preventActions={preventActions}
        />

        {/*  2:   */}
        <GameButton 
          src={rbtn} 
          alt=" " 
          onClick={recapClick} 
          top="73.15%" 
          left="34.2%" 
          width="11.5%" 
          preventActions={preventActions}
        />
      </div>  
    </div>
  );
}

export default GameLayout;