import React from 'react';
import background from '../assets/TestBG.png';
import namebox from '../assets/Name_box.png';
import { useState } from 'react';
import toggle from '../assets/Toggle.png';
import { useNavigate } from 'react-router-dom';

//     "" 
const NextButton = ({ onClick, top, left, width, text, img, preventActions }) => {
  const [pressed, setPressed] = useState(false);
  const rotation = "-1.16deg";
  return (
    <div 
      onClick={onClick}
      //pc 
      onMouseDown={() => setPressed(true)}
      onMouseUp={() => setPressed(false)}
      onMouseLeave={() => setPressed(false)}
      // 
      onTouchStart={() => setPressed(true)}
      onTouchEnd={() => setPressed(false)}
      style={{
        position: 'absolute',
        //   top, left   .
        top: top, 
        left: left,
        width: width, //    
        transform: `translate(-50%, -50%) rotate(${rotation}) scale(${pressed ? 0.95 : 1})`,
        display: 'flex',
        flexDirection: 'row', //  
        alignItems: 'center', 
        justifyContent: 'center',
        gap: '8px', //    
        cursor: 'pointer',
        zIndex: 20,
        transition: 'transform 0.1s ease',
        WebkitTapHighlightColor: 'transparent',
        
      }}
    >
      {/* 1.  ( ) */}
      <img 
        src={toggle} 
        alt="" 
        style={{ width: '100%', display: 'block', }} 
        onContextMenu={preventActions} 
        onDragStart={preventActions} 
      />
      
      {/* 2.  (   ) */}
      <div style={{
        fontFamily: 'SchoolSafetyTteokbokki',
        fontSize: '1.5cqi',
        color: '#7B5323',
        pointerEvents: 'none', //    div  
        whiteSpace: 'nowrap',
      }}>
        {text}
      </div>
    </div>
  );
};

function NamewriteView() {
  const preventActions = (e) => e.preventDefault(); // 

  const [userName, setUserName] = useState(''); //   state

  const navigate = useNavigate();

  return (
    <div style={{ 
      width: '100vw', 
      height: '100vh', 
      backgroundColor: '#ffffff', // 
      display: 'flex', 
      alignItems: 'center', 
      justifyContent: 'center', 
      overflow: 'hidden' 
    }}>

    <div style={{ 
        position: 'relative', 
      //     (16:9)  ' '  
      width: 'min(100vw, calc(100vh * 1920 / 1080))',
      height: 'min(100vh, calc(100vw * 1080 / 1920))',
      backgroundColor: 'black', //   (   )
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      //boxShadow: '0 0 20px rgba(0,0,0,0.2)' //   
    }}>

        <img 
          src={background} 
          alt=""
          style={{
            width: '100%',
            height: '100%',
            //objectFit: 'fill', //   
            display: 'block'
          }}
          onContextMenu={preventActions}
          onDragStart={preventActions}
        />

        <input
            type="text"
            placeholder=" "
            value={userName}
            onChange={(e) => setUserName(e.target.value)}
            style={{
                position: 'absolute',
                top: "37.8%", //   
                left: "68.5%", 
                transform: 'translate(-50%, -50%)',
                width: "20%", //        
                zIndex: 15,    // (10) 
                background: 'transparent', //  
                border: 'none',            //  
                outline: 'none',           //     
                fontFamily: 'SchoolSafetyStubbyChalk', //  
                fontSize: '2cqi',
                color: '#7B5323',          //  
                textAlign: 'center'
            }}
        />

        <img 
          src={namebox} 
          alt=" "
          style={{
            position: 'absolute',
            top: "38%", 
            left: "68.5%", 
            width: "28%",
            transform: 'translate(-50%, -50%)', //    
            zIndex: 10
          }}
          onContextMenu={preventActions}
          onDragStart={preventActions}
        />

        <NextButton 
          text=""
          top="52.8%"
          left="68%"
          width="2.5%" //   
          onClick={() => {
            if(userName.trim() === "") {
              alert(" !");
            } else {
              navigate('/test', { state: { userName } });
              console.log(userName + " !");
            }
          }}
          preventActions={preventActions}
        />

      </div>  
    </div>
  );
}

export default NamewriteView;