import React from 'react';
import background from '../assets/RecapBackground_v2.png';
import nino from '../assets/Nino_Recap.png';
import ment from '../assets/R_ment.png';
import toggle from '../assets/R_Toggle.png';

import { useNavigate } from 'react-router-dom'; // js  
import { useState } from 'react'; // 
import Particles from "react-tsparticles"; // react-particles  react-tsparticles 
import { loadSlim } from "tsparticles-slim"; //
//npm install react-tsparticles tsparticles-slim ->      

// 
const RecapButton = ({ src, alt, onClick, top, left, width, preventActions }) => {
  const [pressed, setPressed] = useState(false);

  return (
    <img 
      src={src} 
      alt={alt}
      onClick={onClick}
      //pc 
      onMouseDown={() => setPressed(true)}
      onMouseUp={() => setPressed(false)}
      onMouseLeave={() => setPressed(false)}
      // 
      onTouchStart={() => setPressed(true)}
      onTouchEnd={() => setPressed(false)}
      style={{
        position: 'absolute',
        top: top,
        left: left,
        transform: `translate(-50%, -50%) scale(${pressed ? 0.92 : 1})`,
        width: width,
        zIndex: 20,
        cursor: 'pointer',
        transition: 'transform 0.1s ease',
        WebkitTapHighlightColor: 'transparent'
      }}
      onContextMenu={preventActions}
      onDragStart={preventActions}
    />
  );
};

function RecapLayout() {
  const preventActions = (e) => e.preventDefault(); // 

  const navigate = useNavigate(); //  

  const nextClick = () => {
    navigate('/recap'); // App.js  path 
  };

  const particlesInit = async (engine) => {
    await loadSlim(engine);
  };

  return (
    <div style={{ 
      width: '100vw', 
      height: '100vh', 
      backgroundColor: '#ffffff', // 
      display: 'flex', 
      alignItems: 'center', 
      justifyContent: 'center', 
      overflow: 'hidden' 
    }}>

    <div style={{ 
        position: 'relative', 
      //     (16:9)  ' '  
      width: 'min(100vw, calc(100vh * 1920 / 1080))',
      height: 'min(100vh, calc(100vw * 1080 / 1920))',
      backgroundColor: 'black', //   (   )
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      //boxShadow: '0 0 20px rgba(0,0,0,0.2)' //   
    }}>

      <RecapButton 
          src={toggle} 
          alt="" 
          onClick={nextClick} 
          top="50.5%" 
          left="95.5%" 
          width="2.4%" 
          preventActions={preventActions}
        />

      <img 
          src={ment} 
          alt=""
          style={{
            position: 'absolute',
            top: '80.4%',          //  50% 
            left: '79.5%',         //  "
            transform: 'translate(-50%, -50%)', //    
            width: '31%',      //   
            zIndex: 10
          }}
          onContextMenu={preventActions}
          onDragStart={preventActions}
        />

        <img 
          src={nino} 
          alt=""
          style={{
            position: 'absolute',
            top: '50%',          //  50% 
            left: '49.95%',         //  "
            transform: 'translate(-50%, -50%)', //    
            width: '100%',      //   
            zIndex: 10
          }}
          onContextMenu={preventActions}
          onDragStart={preventActions}
        />

    <Particles
          id="tsparticles"
          init={particlesInit}
          options={{
            fullScreen: { enable: false }, //    
            fpsLimit: 60,
            particles: {
              number: {
                value: 50, //  
                density: { enable: true, area: 800 },
              },
              color: { value: "#ffffff" },
              shape: { type: "circle" },
              opacity: {
                value: { min: 0.1, max: 0.5 }, //     
                animation: {
                  enable: true,
                  speed: 1,
                  sync: false,
                }
              },
              size: {
                value: { min: 1, max: 3 },
              },
              move: {
                enable: true,
                speed: 0.3, //   
                direction: "none",
                random: true,
                straight: false,
                outModes: { default: "out" },
              },
            },
            // 3. z-index  (   )
            zIndex: {
              value: 10
            },
            detectRetina: true,
          }}
          style={{
            position: "absolute",
            top: 0,
            left: 0,
            width: "100%",
            height: "100%",
          }}
        />

        <img 
          src={background} 
          alt=""
          style={{
            width: '100%',
            height: '100%',
            //objectFit: 'fill', //   
            display: 'block'
          }}
          onContextMenu={preventActions}
          onDragStart={preventActions}
        />
      </div>  
    </div>
  );
}

export default RecapLayout;