import React, { useEffect, useRef } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { useState } from 'react'; // 

import background from '../assets/TestBG.png';
import exclamation_H from '../assets/Exclamation_H.png';
import reaction_H from '../assets/Reaction_H.png';
import encyclopedia_H from '../assets/Encyclopedia_H.png';
import analysis_H from '../assets/Analysis_H.png';
import collecting_H from '../assets/Collecting_H.png';
import creator_H from '../assets/Creator_H.png';
import explorer_H from '../assets/Explorer_H.png';
import promotion_H from '../assets/Promotion_H.png';
import home from '../assets/Home.png';
import recap from '../assets/Recap.png'

const ResultButton = ({ src, alt, onClick, top, left, width, preventActions }) => {
  const [pressed, setPressed] = useState(false);

  return (
    <img 
      src={src} 
      alt={alt}
      onClick={onClick}
      //pc 
      onMouseDown={() => setPressed(true)}
      onMouseUp={() => setPressed(false)}
      onMouseLeave={() => setPressed(false)}
      // 
      onTouchStart={() => setPressed(true)}
      onTouchEnd={() => setPressed(false)}
      style={{
        position: 'absolute',
        top: top,
        left: left,
        transform: `translate(-50%, -50%) scale(${pressed ? 0.92 : 1})`,
        width: width,
        zIndex: 20,
        cursor: 'pointer',
        transition: 'transform 0.1s ease',
        WebkitTapHighlightColor: 'transparent'
      }}
      onContextMenu={preventActions}
      onDragStart={preventActions}
    />
  );
};


function ResultView() {
  const preventActions = (e) => e.preventDefault(); // 

  const navigate = useNavigate();

  const canvasRef = useRef(null);
  const location = useLocation();
  const { finalCounts, userName } = location.state || { finalCounts: { Exclamation_H: 0 }, userName: "" };

  //     key(type)  
  const getBestType = (obj) => {
    if (!obj) return "Exclamation_H";

    // 1.     (:  3)
    const maxScore = Math.max(...Object.values(obj));

    // 2.       (: ["Analysis_H", "Reaction_H"])
    const candidates = Object.keys(obj).filter(key => obj[key] === maxScore);

    // 3.        
    //   0   
    return candidates[Math.floor(Math.random() * candidates.length)];
  };

  const bestType = getBestType(finalCounts); // : "type3" 

  //    
  const results = {
    Exclamation_H: { title: " ", img: exclamation_H },
    Reaction_H: { title: " ", img: reaction_H },
    Encyclopedia_H: { title: " ", img: encyclopedia_H },
    Analysis_H: { title: " ", img: analysis_H },
    Collecting_H: { title: " ", img: collecting_H },
    Creator_H: { title: " ", img: creator_H },
    Explorer_H: { title: " ", img: explorer_H },
    Promotion_H: { title: " ", img: promotion_H }
  };

  const finalResult = results[bestType];

  const testClick = () => {
    navigate('/'); // App.js  path 
  };
  const recapClick = () => {
    navigate('/recap-main'); // App.js  path 
  };

  useEffect(() => {
    const canvas = canvasRef.current;
    const ctx = canvas.getContext('2d');
    const image = new Image();
    image.src = finalResult.img;

    image.onload = () => {
      // 1.     
      canvas.width = image.width;
      canvas.height = image.height;

      const x = canvas.width*0.392;     //  
      const y = canvas.height * 0.41;

      // 2.   
      ctx.drawImage(image, 0, 0);

      // 3.    
      ctx.font = 'bold 80px SchoolSafetyStubbyChalk'; //  
      ctx.textAlign = 'left';

      ctx.lineJoin = 'round'; //   

      // 2.   (: ,  1)
      ctx.strokeStyle = '#7B5323';
      ctx.lineWidth = 16;
      ctx.strokeText(`${userName}`, x, y);  

      // 1.    (: ,  15)
      ctx.strokeStyle = '#FFFFFF';
      ctx.lineWidth = 12;
      ctx.strokeText(`${userName}`, x, y);

          

      // 4.   (   )
      // :      (x: , y:  85%)
      ctx.fillStyle = '#7B5323'; // 
      ctx.fillText(`${userName}`, x,y);
    };
  }, [finalResult, userName]);

  const saveImage = () => {
    const canvas = canvasRef.current;
    const link = document.createElement('a');
    link.download = `${userName}__.png`;
    link.href = canvas.toDataURL();
    link.click();
  };
  
  return (
    <div style={{ 
      width: '100vw', 
      height: '100vh', 
      backgroundColor: '#ffffff', // 
      display: 'flex', 
      alignItems: 'center', 
      justifyContent: 'center', 
      overflow: 'hidden' 
    }}>

    <div style={{ 
        position: 'relative', 
      //     (16:9)  ' '  
      width: 'min(100vw, calc(100vh * 1920 / 1080))',
      height: 'min(100vh, calc(100vw * 1080 / 1920))',
      backgroundColor: 'black', //   (   )
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      //boxShadow: '0 0 20px rgba(0,0,0,0.2)' //   
    }}>

        <img 
          src={background} 
          alt=""
          style={{
            width: '100%',
            height: '100%',
            //objectFit: 'fill', //   
            display: 'block'
          }}
          onContextMenu={preventActions}
          onDragStart={preventActions}
        />
        
        <div style={{ 
            position: 'absolute', 
            top: '47.8%', 
            left: '71%', 
            // 1. transform    ' ' ''  .
            transform: 'translate(-50%, -50%) rotate(-1.16deg)', 
            textAlign: 'center',
            // 2.  div       .
            width: '45%', 
            zIndex: 15
        }}>
        {/*    . */}
        <canvas 
            ref={canvasRef} 
            // 3. style width 100%   div     .
            style={{ 
            width: '100%', 
            maxWidth: '670px', 
            height: 'auto', //   
            cursor: 'pointer',
            display: 'block'
            }} 
            title=" "
            onClick={saveImage} //     
        />
        </div>

        <ResultButton 
                src={home} 
                alt="" 
                onClick={testClick} 
                top="81.2%" 
                left="61%" 
                width="16%" 
                preventActions={preventActions}
            />

            <ResultButton 
                src={recap} 
                alt="" 
                onClick={recapClick} 
                top="80.6%" 
                left="79%" 
                width="16%" 
                preventActions={preventActions}
            />

      </div>  
    </div>
  );
}

export default ResultView;