import React, { useState } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';

import background from '../assets/TestBG.png';
import u_answer from '../assets/U_answer.png';
import d_answer from '../assets/D_answer.png';
import question_n from '../assets/Question_N.svg';
import question from '../assets/Question.svg';
import questions from '../data/Questions'; 
import './QuestionView.css';

// OptionButton  (   )
const OptionButton = ({ text, onClick, top, left, width, preventActions, img }) => {
  const [pressed, setPressed] = useState(false);
  
  return (
    <div 
      onClick={onClick}
      //pc 
      onMouseDown={() => setPressed(true)}
      onMouseUp={() => setPressed(false)}
      onMouseLeave={() => setPressed(false)}
      // 
      onTouchStart={() => setPressed(true)}
      onTouchEnd={() => setPressed(false)}
      style={{
        position: 'absolute',
        top: top,
        left: left,
        // translate(-50%, -50%) scale      .
        transform: `translate(-50%, -50%) scale(${pressed ? 0.96 : 1})`,
        width: width,
        cursor: 'pointer',
        zIndex: 25,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        WebkitTapHighlightColor: 'transparent',
        transition: 'transform 0.1s ease', // scale  transform   
        transformOrigin: 'center center'   //    
      }}
    >
      {/* 1.    */}
      <img 
        src={img} 
        alt=" " 
        style={{ width: '100%', display: 'block' }} 
        onContextMenu={preventActions}
        onDragStart={preventActions}
      />
      
      {/* 2.      */}
      <div className="answer-text" style={{ 
        position: 'absolute',
        fontSize: '2.15cqi',
        color: '#7B5323',
        fontFamily: 'SchoolSafetyStubbyChalk',
        pointerEvents: 'none',
        textAlign: 'left',
        width: '80%',
        lineHeight: '1.2',
        //   
        transform: 'rotate(-1.16deg)',
        letterSpacing: '0em',
        //   (   )
        top: '19%', 
        left: '18%',
      }}>
        {text}
      </div>
    </div>
  );
}

function QuestionView() {
  const preventActions = (e) => e.preventDefault(); // 

  const navigate = useNavigate();
  const location = useLocation()
  const [currentIdx, setCurrentIdx] = useState(0); //    (1 )

  const currentQ = questions[currentIdx]; //    

  const userName = location.state?.userName || "";
  // :    0
  const [counts, setCounts] = useState({
    Exclamation_H: 0, Reaction_H: 0, Encyclopedia_H: 0, Analysis_H: 0,
  Collecting_H: 0, Creator_H: 0, Explorer_H: 0, Promotion_H: 0
  });

  const handleNext = (selectedTypes) => {
    // 1.   
    let newCounts = { ...counts };

    // 2.  ()  1 
    // : ["type1", "type4"]     +1
    selectedTypes.forEach((type) => {
        newCounts[type] = (newCounts[type] || 0) + 1;
    });

    // 3.  
    setCounts(newCounts);

    if (currentIdx < questions.length - 1) {
        setCurrentIdx(currentIdx + 1);
    } else {
        //   
        navigate('/result', { state: { finalCounts: newCounts, userName: userName } });
    }
  };

  const questionNumber = String(currentIdx + 1).padStart(2, '0'); //     

  return (
    <div style={{ 
      width: '100vw', 
      height: '100vh', 
      backgroundColor: '#ffffff', // 
      display: 'flex', 
      alignItems: 'center', 
      justifyContent: 'center', 
      overflow: 'hidden' 
    }}>

    <div style={{ 
        position: 'relative', 
      //     (16:9)  ' '  
      width: 'min(100vw, calc(100vh * 1920 / 1080))',
      height: 'min(100vh, calc(100vw * 1080 / 1920))',
      backgroundColor: 'black', //   (   )
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      //boxShadow: '0 0 20px rgba(0,0,0,0.2)' //   
    }}>

        <img 
          src={background} 
          alt=""
          style={{
            width: '100%',
            height: '100%',
            //objectFit: 'fill', //   
            display: 'block'
          }}
          onContextMenu={preventActions}
          onDragStart={preventActions}
        />

        <div 
            className="question-text"
            style={{
            position: 'absolute',
            top: '28.5%', //      
            left: '53.95%',
            transform: 'translate(-50%, -50%) rotate(1.16deg)', //   
            fontFamily: 'SchoolSafetyTteokbokki',
            fontSize: '2.15cqi', //   
            color: '#ffffff',
            zIndex: 30
        }}>
            {questionNumber}
        </div>

        <img 
          src={question_n} 
          alt=""
          style={{
            position: 'absolute',
            top: '29%',          //  50% 
            left: '54%',         //  "
            transform: 'translate(-50%, -50%)', //    
            width: '3.8%',      //   
            zIndex: 15
          }}
          onContextMenu={preventActions}
          onDragStart={preventActions}
        />
        
        <div 
            className="question-text"
            style={{
            position: 'absolute',
            top: '33.5%', //       
            left: '69.3%',
            transform: 'translate(-50%, -50%) rotate(-1.16deg)',
            width: '70%',
            textAlign: 'center',
            fontSize: '2.2cqi', //    
            color: '#ffffff',
            zIndex: 15,
            lineHeight: '1.4',
            wordBreak: 'keep-all'
            }}>
            {currentQ.question}
        </div>

        <img 
          src={question} 
          alt=""
          style={{
            position: 'absolute',
            top: '33.845%',          //  50% 
            left: '69.25%',         //  "
            transform: 'translate(-50%, -50%)', //    
            width: '39%',      //   
            zIndex: 10
          }}
          onContextMenu={preventActions}
          onDragStart={preventActions}
        />

        <OptionButton 
            text={currentQ.options[0].text} 
            onClick={() => handleNext(currentQ.options[0].value)}
            top="51.75%"   
            left="69.55%" 
            width="39%" 
            img={u_answer} 
            preventActions={preventActions}
        />

        <OptionButton 
            text={currentQ.options[1].text} 
            onClick={() => handleNext(currentQ.options[1].value)}
            top="64.2%"    
            left="69.62%" 
            width="39%" 
            img={d_answer} 
            preventActions={preventActions}
        />

      </div>  
    </div>
  );
}

export default QuestionView;